const form = document.querySelector(`.application-form__form`);

if (form) {
  const projectSelectInput = document.getElementById(`project`);
  const emailInput = document.getElementById(`email`);
  const validationAnswerBlock = document.querySelector(`.application-form__validation-answer`);
  const answerTextBlock = document.querySelector(`.application-form__answer__text`);

  const checkValid = function () {
    if (form.checkValidity()
    && emailInput.value.search(/^((([0-9A-Za-z]{1}[-0-9A-z\.]{1,}[0-9A-Za-z]{1})|([0-9А-Яа-я]{1}[-0-9А-я\.]{1,}[0-9А-Яа-я]{1}))@([-A-Za-z]{1,}\.){1,2}[-A-Za-z]{2,})$/u) !== -1
    && projectSelectInput.value !== `0`) {
      return true;
    }

    let answerText = [];

    if (emailInput.value.search(/^((([0-9A-Za-z]{1}[-0-9A-z\.]{1,}[0-9A-Za-z]{1})|([0-9А-Яа-я]{1}[-0-9А-я\.]{1,}[0-9А-Яа-я]{1}))@([-A-Za-z]{1,}\.){1,2}[-A-Za-z]{2,})$/u) === -1) {
      answerText.push(`Введите корректный email`);
    }

    if (projectSelectInput.value === `0`) {
      answerText.push(`Выберите проект`);
    }

    return answerText;
  };

  const onFormSubmit = function (e) {
    e.preventDefault();
    const resultValidation = checkValid();

    if (resultValidation === true) {

      form.submit();
      return;
    }

    validationAnswerBlock.classList.remove(`application-form__validation-answer--success`);
    validationAnswerBlock.classList.add(`application-form__validation-answer--active`, `application-form__validation-answer--failing`);
    answerTextBlock.innerHTML = ``;

    resultValidation.forEach((textError) => {
      const text = document.createElement(`p`);
      text.textContent = textError;

      answerTextBlock.appendChild(text);
    });
  };

  form.addEventListener(`submit`, onFormSubmit);
}
